import Vue from 'vue'
import Router from 'vue-router'

//登录
import Login from '@/components/Login'
//主界面
import Home from '@/components/Home'

//挂号收费人员
import Register from '@/components/neureg/Register' //现场挂号
import Charge   from '@/components/neureg/Charge'   //收费
import Refund   from '@/components/neureg/Refund'   //退费
import Signout  from '@/components/neureg/Signout'  //退号
import Makeup   from '@/components/neureg/Makeup'   //发票补打
import Replay   from '@/components/neureg/Replay'   //发票重打
import Daily    from '@/components/neureg/Daily'     //日结
import Daily2   from '@/components/neureg/Daily2'    //日结查询
import Patient from '@/components/neureg/Patient'    //患者费用查询

//财务管理
import Count01  from '@/components/neufin/Count01'  //门诊医生工作量统计
import Count02  from '@/components/neufin/Count02'  //开单科室工作量统计
import Count03  from '@/components/neufin/Count03'  //执行科室工作量统计
import Cost     from '@/components/neufin/Cost'     //费用科目管理

//门诊医生
import docHome from '@/components/neudoc/home'              //门诊医生
import suite01 from '@/components/neudoc/his2/suite01.vue'  //病历模板管理
import suite02 from '@/components/neudoc/his2/suite02.vue'  //检查模板管理
import suite03 from '@/components/neudoc/his2/suite03.vue'  //检验模板管理
import suite04 from '@/components/neudoc/his2/suite04.vue'  //处置模板管理

//医技处置
import tech01 from '@/components/neuski/tech01.vue'  //患者检验
import tech02 from '@/components/neuski/tech02.vue'  //患者检查
import tech03 from '@/components/neuski/tech03.vue'  //患者处置
import tech04 from '@/components/neuski/tech04.vue'  //医技管理

//药房管理
import shop01 from '@/components/neupha/shop01.vue'  //药房发药
import shop02 from '@/components/neupha/shop02.vue'  //药房退药
import shop03 from '@/components/neupha/shop03.vue'  //药品管理

//系统管理
import Constant from '@/components/neusys/Constant'             //常数类别管理
import Dept from '@/components/neusys/Dept'                     //科室管理
import User from '@/components/neusys/User'                     //用户管理
import RegistLevel from '@/components/neusys/RegistLevel'       //挂号级别管理
import SettleCategory from '@/components/neusys/SettleCategory' //结算类别管理
import DiseCategory from '@/components/neusys/DiseCategory'     //诊断目录管理
import Project from '@/components/neusys/Project'               //非药品收费项目管理

import Scheduling from '@/components/neusys/Scheduling_new' //医生排班管理 new

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/login',
      name: '登录',
      hidden: true,
      component: Login,  //Login   Home
      meta: {
        isLogin: false
      }
    },
    {
      path: '/home',
      name: '',
      component: Home,
      hidden: true,
      meta: {
        isLogin: true
      }
    }, {
      path: '/home',
      component: Home,
      name: '挂号收费',
      iconCls: 'fa fa-file-text-o',
      children: [
        {
          path: '/Register',
          name: '现场挂号',
          component: Register,
          meta: {
            keepAlive: false,
            isLogin: true
          }
        }, {
          path: '/Signout',
          name: '退号',
          component: Signout,
          meta: {
            keepAlive: false,
            isLogin: true
          }
        },{
          path: '/Charge',
          name: '收费',
          component: Charge,
          meta: {
            keepAlive: false,
            isLogin: true
          }
        }, {
          path: '/Refund',
          name: '退费',
          component: Refund,
          meta: {
            keepAlive: false,
            isLogin: true
          }
        },{
          path: '/Makeup',
          name: '发票补打',
          component: Makeup,
          meta: {
            keepAlive: false,
            isLogin: true
          }
        },{
          path: '/Replay',
          name: '发票重打',
          component: Replay,
          meta: {
            keepAlive: false,
            isLogin: true
          }
        },{
          path: '/Patient',
          name: '患者费用查询',
          component: Patient,
          meta: {
            keepAlive: false,
            isLogin: true
          }
        },{
          path: '/Daily',
          name: '日结*',
          component: Daily,
          meta: {
            keepAlive: false,
            isLogin: true
          }
        },{
          path: '/Daily2',
          name: '日结查询*',
          component: Daily2,
          meta: {
            keepAlive: false,
            isLogin: true
          }
        }
      ]
    },{
      path: '/home',
      component: Home,
      name: '门诊医生',
      children: [
        {
          path: '/docHome',
          iconCls: 'fa fa-user-o',
          name: '门诊病历',
          component: docHome,
          meta: {
            keepAlive: false,
            isLogin: true
          }
        },{
          path: '/suite02',
          name: '医技模板管理',
          component: suite02,
          meta: {
            keepAlive: false,
            isLogin: true
          }
        },{
          path: '/suite03',
          name: '西药处方模板',
          component: suite03,
          meta: {
            keepAlive: false,
            isLogin: true
          }
        },{
          path: '/suite04',
          name: '中药处方模板*',
          component: suite04,
          meta: {
            keepAlive: false,
            isLogin: true
          }
        },
      ]
    },{
      path: '/home',
      component: Home,
      name: '医技处置*',
      iconCls: 'fa fa-user-o',
      children: [
        {
          path: '/tech01',
          name: '患者检验*',
          component: tech01,
          meta: {
            keepAlive: false,
            isLogin: true
          }
        },{
          path: '/tech02',
          name: '患者检查*',
          component: tech02,
          meta: {
            keepAlive: false,
            isLogin: true
          }
        },{
          path: '/tech03',
          name: '患者处置*',
          component: tech03,
          meta: {
            keepAlive: false,
            isLogin: true
          }
        },{
          path: '/tech04',
          name: '医技管理*',
          component: tech04,
          meta: {
            keepAlive: false,
            isLogin: true
          }
        }]},{
      path: '/home',
      component: Home,
      name: '药房管理',
      iconCls: 'fa fa-user-o',
      children: [
        {
          path: '/shop01',
          name: '药房发药',
          component: shop01,
          meta: {
            keepAlive: false,
            isLogin: true
          }
        },{
          path: '/shop02',
          name: '药房退药',
          component: shop02,
          meta: {
            keepAlive: false,
            isLogin: true
          }
        },{
          path: '/shop03',
          name: '药品管理',
          component: shop03,
          meta: {
            keepAlive: false,
            isLogin: true
          }
        }]
    },{
      path: '/home',
      component: Home,
      name: '财务管理',
      iconCls: 'fa fa-user-o',
      children: [
        {
          path: '/Count01',
          name: '门诊医生工作量统计*',
          component: Count01,
          meta: {
            keepAlive: false,
            isLogin: true
          }
        },{
          path: '/Count02',
          name: '开单科室工作量统计*',
          component: Count02,
          meta: {
            keepAlive: false,
            isLogin: true
          }
        },{
          path: '/Count03',
          name: '执行科室工作量统计*',
          component: Count03,
          meta: {
            keepAlive: false,
            isLogin: true
          }
        },{
          path: '/Cost',
          name: '费用科目管理',
          component: Cost,
          meta: {
            keepAlive: false,
            isLogin: true
          }
        }
      ]
    } ,{
      path: '/home',
      component: Home,
      name: '系统信息',
      iconCls: 'fa fa-user-o',
      children: [
        {
          path: '/Constant',
          name: '常数类别管理',
          component: Constant,
          meta: {
            keepAlive: false,
            isLogin: true
          }
        },{
          path: '/Dept',
          name: '科室管理',
          component: Dept,
          meta: {
            keepAlive: false,
            isLogin: true
          }
        },{
          path: '/User',
          name: '用户管理',
          component: User,
          meta: {
            keepAlive: false,
            isLogin: true
          }
        },{
          path: '/RegistLevel',
          name: '挂号级别管理',
          component: RegistLevel,
          meta: {
            keepAlive: false,
            isLogin: true
          }
        },{
          path: '/SettleCategory',
          name: '结算类别管理',
          component: SettleCategory,
          meta: {
            keepAlive: false,
            isLogin: true
          }
        },{
          path: '/DiseCategory',
          name: '诊断目录管理',
          component: DiseCategory,
          meta: {
            keepAlive: false,
            isLogin: true
          }
        },{
          path: '/Project',
          name: '非药品收费项目管理',
          component: Project,
          meta: {
            keepAlive: false,
            isLogin: true
          }
        },{
          path: '/Scheduling',
          name: '医生排班管理',
          component: Scheduling,
          meta: {
            keepAlive: false,
            isLogin: true
          }
        }
      ]
    }
  ]
})



